.App .login {
  overflow: hidden;
}
.App .login .login-bg {
  position: fixed;
  width: 100%;
}
.App .login .main {
  margin: 180px 20px 20px;
}
.App .login .main .box {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  overflow: hidden;
}
.App .login .main .box .am-list-item .am-list-line .am-input-label {
  color: #ff8e45;
}
.App .login .main .box .am-list-item .am-list-line .am-input-control input {
  color: #666 !important;
}
.App .login .main .box .am-list-item:first-child {
  padding: 10px 20px 0;
}
.App .login .main .box .am-list-item:nth-child(2) {
  padding: 0 20px 10px;
}
.App .login .main .box .am-list-item:nth-child(2) .am-list-line:after {
  display: none;
}
.App .login .main .am-button {
  background: linear-gradient(145deg, #81c7e2, #4fd8ab);
}
.App .login .main .am-button:before {
  border: none;
}
.App .login .main .am-button span {
  color: #fff;
}
.App .home .c-title {
  color: #999;
}
.App .home .am-grid .am-flexbox .am-grid-item:before {
  padding-bottom: 80%!important;
}
.App .home .am-grid .am-flexbox .am-flexbox-item .am-grid-icon {
  width: auto!important;
  height: 50px!important;
}
.App .home .am-grid .am-flexbox .am-flexbox-item .am-grid-text {
  font-size: 14px!important;
}
.App .braceletActivate .am-wingblank .slider .slider-frame .slider-list,
.App .braceletElectric .am-wingblank .slider .slider-frame .slider-list {
  height: 150px!important;
}
.App .braceletActivate .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box,
.App .braceletElectric .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box {
