}
.App .braceletActivate .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box .main i .am-progress-outer,
.App .braceletElectric .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box .main i .am-progress-outer {
  position: static;
  width: 100%;
  margin-top: 0;
  border-radius: 100px;
  overflow: hidden;
}
.App .braceletActivate .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box .main i .am-progress-outer .am-progress-bar,
.App .braceletElectric .am-wingblank .slider .slider-frame .slider-list .slider-slide .percent-box .main i .am-progress-outer .am-progress-bar {
  border-width: 6px!important;
}
.App .notFound404 {
  text-align: center;
}
.App .notFound404 img {
  width: 140px;
  margin: 60px 0 30px;
}
.App .notFound404 p {
  color: #999;
}
.App .braceletActivateDetails table,
.App .braceletElectricDetails table,
.App .d-table table {
