.router > div {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.router .slide-left-enter {
  transform: translate(100vw, 0);
}
.router .slide-right-exit-active {
  transition: transform 0.3s ease;
  transform: translate(100vw, 0);
}
.router .slide-right-enter {
  transform: translate(-100vw, 0);
}
.router .slide-left-exit-active {
  transition: transform 0.3s ease;
  transform: translate(-100vw, 0);
}
