.am-modal-wrap .am-modal .clock {
  margin: -5px 0 -20px;
}
.am-modal-wrap .am-modal .clock th {
  padding: 5px 0;
  color: #7794a9;
  font-weight: normal;
}
.am-modal-wrap .am-modal .clock td {
  font-size: 16px!important;
  color: #00365d;
  padding: 3px 10px;
}
.am-modal-wrap .am-modal .clock td em {
  color: #108ee9;
  margin-right: 8px;
}
.am-modal-wrap .am-modal .clock td p {
  line-height: 30px;
}
.am-whitespace {
  background: #eff7fd;
}
h3.title {
  line-height: 50px;
  padding: 0 20px;
  box-sizing: border-box;
}
h3.title i {
  float: right;
  color: #999;
  max-width: 50%;
  white-space: nowrap;
  overflow-y: hidden;
}
h3.title i.iconfont {
  font-size: 20px;
  color: #607d8b;
}
h3.title em.iconfont {
  font-size: 26px;
  vertical-align: middle;
  margin-right: 5px;
  color: #607d8b;
}
h3.remark {
  height: 30px;
  line-height: 20px;
  background: #eff7fd;
  color: #999;
  padding: 0 20px;
  font-size: 14px;
}
.pick-box .am-list-extra,
.pick-box .am-list-content {
  color: #888 !important;
  flex-basis: auto!important;
  font-size: 14px!important;
}
.no-data {
  text-align: center;
  color: #c4d8f5;
}
.no-data:before {
  content: '';
  display: block;
  margin: 40px auto 20px;
  width: 200px;
  height: 90px;
  background: url("~@/assets/images/noData.png") no-repeat 5px 0;
  background-size: 100% auto;
}
.d-table {
  width: 100%;
}
.d-table tr td {
  padding: 10px;
  text-align: center;
}
.d-table tr td i {
  padding: 2px 4px;
  border-radius: 2px;
  font-size: 12px;
  color: #fff !important;
}
.pop-title {
  font-size: 16px;
  color: #2196f3;
  text-align: left;
}
.pop-title i {
  float: right;
  color: #999;
}
.tab-title {
  text-align: right;
  margin-top: 15px;
}
.tab-title i {
  padding: 4px 6px;
  margin-left: 10px;
  border-radius: 3px;
  background: #ddd;
  color: #fff;
}
.tab-title i.on {
  background: #3398db;
}
