@charset "utf-8";
body{margin: 0;background: #fff;}
html{background-color:#fff;color:#333;font-weight: normal;font-size:14px;font-family: "Microsoft Yahei", '方正兰亭超细黑简', tahoma,arial,"Hiragino Sans GB",simsun,sans-serif;}
a, i, em{text-decoration: none;color: #333;font-weight: normal;font-style: normal;font-size: 14px;}
h1,h2,h3,h4,h5,p{font-weight: normal;margin: 0;}
ul,li,ol{list-style:none;margin: 0;padding: 0;}
input,button{-webkit-appearance:none;outline:none}
/*去掉input自动补全后的黄色样式*/
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active {
-webkit-transition-delay: 99999s;
-webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
/* -webkit-box-shadow: 0 0 0px 1000px white inset; */
}